* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: whitesmoke;
}

.head1 {
  width: 80%;
  height: 100px;
  background-color: #4d308c;
  border-radius: 100px;
  padding-bottom: 20px;
  overflow: hidden;
  margin: 10px auto;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 40px;
}

.logo-container {
  flex-grow: 1;
  display: flex;
  float: left;
}

.logo {
  font-family: monospace;
  font-size: 40px;
  font-weight: bolder;
  color: white;
  text-decoration: none;
}

.head1 img {
  width: 50px;
  height: 50px;
  margin-right: 80px;
}

.right-container-navbar {
  display: flex;
  gap: 2px;
  align-items: center;
}

.right-container-navbar a {
  text-decoration: none;
  padding: 10px 20px;
  color: white;
}

.right-container-navbar a:hover {
  color: rgb(188, 191, 40);
  transition: color 0.4s ease-out;
  transform: translateY(-5px);
}

nav button {
  width: 100px;
  height: 50px;
  color: #7c3aed;
  background-color: #f5e6d3;
  border-radius: 20px;
  margin-bottom: 10px;
}
nav button:hover {
  transition: 0.2s ease-in;
  background-color: beige;
  color: black;
  cursor: pointer;
}
/* Navbar.css */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropbtn {
  background-color: #4CAF50;
  color: white;
  padding: 8px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content button {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  border: none;
  background: none;
  cursor: pointer;
}

.dropdown:hover .dropdown-content {
  display: block;
}

